.game-tag {
    padding: 2px 10px;
    font-size: 13px;
    border-radius: 10px;
    background-color: #eaeaea;

    /* TODO dark theme */
    /*background-color: rgb(80, 82, 84);*/
}

.game-tag:not(:last-child) {
    margin: 0 7px 0 0;
}

.game-tag--active {
    background-color: royalblue;
    color: white;
}

.game-tag--pointer {
    cursor: pointer;
}